<template>
    <div class="fillcontain">
        <head-top></head-top>
        <div class="table_container">
            <el-table
                :data="tableData"
                style="width: 100%">
                <el-table-column type="expand">
                  <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand">
                      <el-form-item label="食品名称">
                        <span>{{ props.row.name }}</span>
                      </el-form-item>
                      <el-form-item label="餐馆名称">
                        <span>{{ props.row.restaurant_name }}</span>
                      </el-form-item>
                      <el-form-item label="食品 ID">
                        <span>{{ props.row.item_id }}</span>
                      </el-form-item>
                      <el-form-item label="餐馆 ID">
                        <span>{{ props.row.restaurant_id }}</span>
                      </el-form-item>
                      <el-form-item label="食品介绍">
                        <span>{{ props.row.description }}</span>
                      </el-form-item>
                      <el-form-item label="餐馆地址">
                        <span>{{ props.row.restaurant_address }}</span>
                      </el-form-item>
                      <el-form-item label="食品评分">
                        <span>{{ props.row.rating }}</span>
                      </el-form-item>
                      <el-form-item label="食品分类">
                        <span>{{ props.row.category_name }}</span>
                      </el-form-item>
                      <el-form-item label="月销量">
                        <span>{{ props.row.month_sales }}</span>
                      </el-form-item>
                    </el-form>
                  </template>
                </el-table-column>
                <el-table-column
                  label="食品名称"
                  prop="name">
                </el-table-column>
                <el-table-column
                  label="食品介绍"
                  prop="description">
                </el-table-column>
                <el-table-column
                  label="评分"
                  prop="rating">
                </el-table-column>
                <el-table-column label="操作" width="160">
                  <template slot-scope="scope">
                    <el-button
                      size="small"
                      @click="handleEdit">编辑</el-button>
                    <el-button
                      size="small"
                      type="danger"
                      @click="handleDelete(scope.$index, tableData)">删除</el-button>
                  </template>
                </el-table-column>
            </el-table>
            <el-dialog title="修改食品信息" :visible.sync="dialogFormVisible">
                <el-form :model="tableData">
                    <el-form-item label="食品名称" label-width="100px">
                        <el-input v-model="tableData.name" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="食品介绍" label-width="100px">
                        <el-input v-model="tableData.description"></el-input>
                    </el-form-item>
                    <el-form-item label="食品分类" label-width="100px">
                        <el-select placeholder="请选择分类" v-model="selectTable.classification">
                        <el-option label="烤鸡" value="123"></el-option>
                        <el-option label="烧烤" value="456"></el-option>
                        <el-option label="甜点" value="123"></el-option>
                        <el-option label="茶水" value="456"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="updateFood">确 定</el-button>
              </div>
            </el-dialog>
        </div>
    </div>
</template>

<script>
    import headTop from '../public/headTop'
    export default {
        data(){
            return {
                tableData: [
                    {
                        description: '好吃不厌',
                        name: '辣条',
                        rating: '9',
                        restaurant_name:"一样",
                        item_id:"1",
                        month_sales:"2533",
                        restaurant_address:"北京市朝阳区建国路18号珠江绿洲家园内广场西侧会所1层",
                        category_name:"广场",
                        restaurant_id:"222"
                    }, 
                    {
                        description: '不酸不辣不烫',
                        name: '麻辣烫',
                        rating: '9.8',
                        restaurant_name:"一样",
                        item_id:"2",
                        month_sales:"111",
                        restaurant_address:"北京市朝阳区建国路18号珠江绿洲家园内广场西侧会所1层",
                        category_name:"广场",
                        restaurant_id:"222"
                    }, 
                    {
                        description: '葱姜蒜放多',
                        name: '蚬子肉',
                        rating: '9.5',
                        restaurant_name:"一样",
                        item_id:"3",
                        month_sales:"22245",
                        restaurant_address:"北京市朝阳区建国路18号珠江绿洲家园内广场西侧会所1层",
                        category_name:"广场",
                        restaurant_id:"222"
                    }, 
                    {
                        description: '无敌是多么寂寞',
                        name: '鲨鱼肉',
                        rating: '10',
                        restaurant_name:"一样",
                        item_id:"4",
                        month_sales:"545",
                        restaurant_address:"北京市朝阳区建国路18号珠江绿洲家园内广场西侧会所1层",
                        category_name:"广场",
                        restaurant_id:"222"
                    }
                ],
                selectTable: {},
                dialogFormVisible: false,
                selectMenu: {
                    label:"吃香喝辣"
                },
                specsForm: {
		          	specs: '',
		          	packing_fee: 0,
		          	price: 20,
		        },
                specsFormrules: {
		        	specs: [
						{ required: true, message: '请输入规格', trigger: 'blur' },
					],
		        },
		        specsFormVisible: false,
                expendRow: [],
            }
        },
    	components: {
    		headTop,
    	},
        methods: {
            handleDelete(index, rows){
                rows.splice(index, 1);
                this.$message.success('删除成功');
            },
            handleEdit(){
                this.dialogFormVisible = true;
            },
            addspecs(){
                this.dialogFormVisible = false;
            },
            updateFood(){
                this.dialogFormVisible = false;
            }
        },
    }
</script>

<style lang="less">
	@import '../style/mixin';
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .table_container{
        padding: 20px;
    }
    .Pagination{
        display: flex;
        justify-content: flex-start;
        margin-top: 8px;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;
    }
</style>



