<template>
  <div class="fillcontain">
    <head-top></head-top>
    <div class="table_container">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="expand">
          <template>
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="店铺名称">
                <span>{{ name }}</span>
              </el-form-item>
              <el-form-item label="店铺地址">
                <span>{{ address }}</span>
              </el-form-item>
              <el-form-item label="店铺介绍">
                <span>{{ description }}</span>
              </el-form-item>
              <el-form-item label="店铺 ID">
                <span>{{ id }}</span>
              </el-form-item>
              <el-form-item label="联系电话">
                <span>{{ phone }}</span>
              </el-form-item>
              <el-form-item label="评分">
                <span>{{ rating }}</span>
              </el-form-item>
              <el-form-item label="销售量">
                <span>{{ recent_order_num }}</span>
              </el-form-item>
              <el-form-item label="分类">
                <span>{{ category }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="店铺名称" prop="name"></el-table-column>
        <el-table-column label="店铺地址" prop="address"></el-table-column>
        <el-table-column label="店铺介绍" prop="description"></el-table-column>
        <el-table-column label="操作" width="200">
          <template slot-scope="scope">
            <el-button size="small" @click="edit">编辑</el-button>
            <router-link to="/addGoods">
              <el-button size="small">添加食品</el-button>
            </router-link>
            <el-button size="small" type="danger" @click="handleDelete(scope.$index, tableData)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="修改店铺信息" :visible.sync="dialogFormVisible">
        <el-form :model="selectTable">
          <el-form-item label="店铺名称" label-width="100px">
            <el-input v-model="selectTable.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="详细地址" label-width="100px">
              <el-input v-model="selectTable.address" auto-complete="off"></el-input>
              <span>当前城市：{{city.name}}</span>
          </el-form-item>
          <el-form-item label="店铺介绍" label-width="100px">
            <el-input v-model="selectTable.description"></el-input>
          </el-form-item>
          <el-form-item label="联系电话" label-width="100px">
            <el-input v-model="selectTable.phone"></el-input>
          </el-form-item>
          <el-form-item label="店铺分类" label-width="100px">
            <el-select placeholder="请选择分类" v-model="selectTable.classification">
              <el-option label="烤鸡" value="123"></el-option>
              <el-option label="烧烤" value="456"></el-option>
              <el-option label="甜点" value="123"></el-option>
              <el-option label="茶水" value="456"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="updateShop">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import headTop from "../public/headTop";
export default {
  data() {
    return {
      city: {
        name:"广州"
      },
      dialogFormVisible: false,
      selectTable: {
        name:"效果演示",
        description:"黄焖鸡米饭",
        phone:"333-444-555",
        address:"广东省广州市越秀区中山五路219号华联购物中心F1"
      },

      tableData: [
        {
          description: '黄焖鸡米饭',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          id:1
        }, 
        {
          description: '一粥七天',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄',
          id:2
        },
        {
          description: '品汇轩便当',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄',
          id:3
        }, 
        {
          description: '海鲜店',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
          id:4
        }
      ],

      name:"效果演示",
      address:"广东省广州市越秀区中山五路219号华联购物中心F1",
      description:"张亮麻辣烫",
      id:'007',
      phone:'333-444-555',
      rating:"良好",
      recent_order_num:"23001/月",
      category:"食品",

      
    };
  },
  components: {
    headTop
  },
  methods: {
    handleDelete(index, rows) {
      rows.splice(index, 1);
      this.$message.success('删除成功');
    },
    updateShop() {
      this.dialogFormVisible = false;
    },
    edit(){
      this.dialogFormVisible = true;
      // console.log(123);
    }
}
}
</script>

<style lang="less" scoped>
@import "../style/mixin";
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.table_container {
  padding: 20px;
}
.Pagination {
  display: flex;
  justify-content: flex-start;
  margin-top: 8px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #20a0ff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 120px;
  height: 120px;
  line-height: 120px;
  text-align: center;
}
.avatar {
  width: 120px;
  height: 120px;
  display: block;
}
.el-button--small, .el-button--small.is-round {
    padding: 5px 8px;
}
</style>



