<template>
    <div>
        <head-top></head-top>
        <visitor-pie :pieData="pieData"></visitor-pie>
    </div>
</template>

<script>
	import headTop from '../public/headTop'
    import visitorPie from '@/public/visitorPie'
    export default {
    	data(){
    		return {
    			pieData: {
					beijing : 335,
					shanghai : 310,
					shenzhen : 234,
					hangzhou : 135,
					qita : 1548
				},
    		}
    	},
    	components: {
    		headTop,
            visitorPie,
    	},
    	mounted(){
    		this.initData();
    	},
    	methods: {
    			
    	}
    }
</script>

<style lang="less">
	@import '../style/mixin';
	
</style>



